import React, { useEffect, useState } from "react";

import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Grid from "@material-ui/core/Grid";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import "./App.css";

import AccountTable from "./components/AccountTable";
import TransactionTable from "./components/TransactionTable";
import DateViewTable from "./components/DateViewTable";
import CurrencyTable from "./components/CurrencyTable";
import ActivityTable from "./components/ActivityTable";

import {
	BrowserRouter as Router,
	Switch,
	Route,
	NavLink,
	useLocation,
	useHistory,
} from "react-router-dom";

import firebase from "./components/firebase";

const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
	},
	menuButton: {
		marginRight: theme.spacing(2),
	},
	title: {
		flexGrow: 1,
		textTransform: "lowercase",
	},
}));

function App() {
	const classes = useStyles();
	const [user, setUser] = useState();
	const [firebaseInitialized, setfirebaseInitialized] = useState();
	var result;

	useEffect(() => {
		firebase.isInitialized().then((val) => {
			if (val) {
				setfirebaseInitialized(val);
			} else {
				setfirebaseInitialized(false);
			}
		});
	});

	useEffect(() => {
		if (firebaseInitialized && firebaseInitialized !== false) {
			result = firebase.findUser(firebaseInitialized.email);
			result.then(function (res) {
				setUser(res);
			});
		}
	}, [firebaseInitialized]);

	return (
		<div className="App">
			<AppBar position="static">
				<Toolbar>
					<Grid
						style={{ display: "flex" }}
						justify="space-between"
						alignItems="center"
						container
						spacing={0}
						className={classes.title}
					>
						<Grid item>
							<NavLink
								activeClassName="active-link"
								to="/accounts"
								style={{
									textDecoration: "none",
									color: "white",
								}}
							>
								<Button
									color="inherit"
									className="item"
									style={{
										textTransform: "initial",
									}}
								>
									accounts
								</Button>
							</NavLink>
							<NavLink
								activeClassName="active-link"
								to="/transactions"
								style={{
									textDecoration: "none",
									color: "white",
								}}
							>
								<Button
									color="inherit"
									className="item"
									style={{
										textTransform: "initial",
									}}
								>
									transactions
								</Button>
							</NavLink>
							<NavLink
								activeClassName="active-link"
								to="/date-view"
								style={{
									textDecoration: "none",
									color: "white",
								}}
							>
								<Button
									color="inherit"
									className="item"
									style={{
										textTransform: "initial",
									}}
								>
									date view
								</Button>
							</NavLink>
							{/* <NavLink
								activeClassName="active-link"
								to="/currencies"
								style={{
									textDecoration: "none",
									color: "white",
								}}
							>
								<Button
									color="inherit"
									className="item"
									style={{
										textTransform: "initial",
									}}
								>
									currencies
								</Button>
							</NavLink> */}
							<NavLink
								activeClassName="active-link"
								to="/activity"
								style={{
									textDecoration: "none",
									color: "white",
								}}
							>
								<Button
									color="inherit"
									className="item"
									style={{
										textTransform: "initial",
									}}
								>
									activity
								</Button>
							</NavLink>
						</Grid>
					</Grid>
				</Toolbar>
			</AppBar>

			<Switch>
				<Route path="/accounts">
					<AccountTable />
				</Route>
				<Route path="/transactions">
					<TransactionTable />
				</Route>
				<Route path="/date-view">
					<DateViewTable />
				</Route>
				{/* <Route path="/currencies">
					<CurrencyTable />
				</Route> */}
				<Route path="/activity">
					<ActivityTable />
				</Route>
			</Switch>
		</div>
	);
}

export default App;
